.text-input-container {
  margin-top: 1rem;
}

.text-input-container:first-child {
  margin-top: 0;
}

.text-xs {
  font-size: 0.75rem;
}
.text-sm {
  font-size: 0.875rem;
}

.valid input[type="email"],
.valid input[type="text"],
.valid input[type="number"],
.valid select,
.valid textarea {
  border-color: green;
}

.valid .feedback {
  color: green;
}

.invalid input[type="email"],
.invalid input[type="text"],
.invalid input[type="number"],
.invalid select,
.invalid textarea {
  border-color: red;
}

.invalid .feedback {
  color: red;
}
